.container {
  width: 400px;
  margin: 0 auto;
}

h1{
 text-align : center;
}

.input-amount,
.input-from,
.input-to {
  margin-bottom: 20px;
}

input,
select {
  width: 100%;
  padding: 10px;
}

label {
  display: block;
  margin-bottom: 5px;
}

select {
  background: #fff;
  border: 1px solid #ccc;
}

.btn {
  background: blue;
  color: white;
  padding: 0.5rem 1rem;
  border: none;
  cursor: pointer;
}

.btn:hover {
  background: lightblue;
}

.output {
  margin-top: 20px;
  font-size: 20px;
  font-weight: bold;
  margin-left : 45%;
}
